
          @import "src/styles/_variables.scss";
          @import "src/styles/_mixins.scss";
        

.footer {
    p {
        font-size: 24px;
        text-align: left;
        color: #fff;
        font-weight: 300;
    }
    img {
        width: 100%;
        height: 100%;
        border-radius: 0;
    }
    background-color: $color-main;
    .main {
        max-width: 1200px;
        margin: 0 auto;
        padding: 40px 0;
        display: flex;
        flex-wrap: nowrap;
    }
    p.title {
        font-weight: 700;
    }
    .left {
        width: 70%;
        display: flex;
        .working {
            padding-left: 20px;
        }
        .logo {
            img{
                border-radius: 8px;
            }
            width: 60%;
            max-width: 350px;
        }
    }
    .bottom {
        width: 100%;
        img {
            width: 100%;
        }
    }
    .right {
        width: 30%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .contact {
        //margin-left: 30%;
    }
    .social {
        display: flex;
        column-gap: 10px;
        margin-top: 20px;
        .item {
            aspect-ratio: 1;
            max-width: 60px;
            width: calc(100% / 6);
            transition: all 0.2s linear;
            &:hover {
                opacity: 0.8;
            }
        }
    }
    @include mobile {
        .main {
            flex-direction: column;
            padding: 30px 10px;
            row-gap: 10px;
            & > div {
                width: 100%;
                justify-content: center;
            }
        }
    }
}