
          @import "src/styles/_variables.scss";
          @import "src/styles/_mixins.scss";
        
$color-main: rgba(51, 55, 64, 1);
$border-image: 20px;

.quiz {
  margin: 100px auto 0 auto;
  text-align: center;
  width: 60%;
  p {
    text-align: center;
  }
  .quiz__container, .delayed-visibility {
    width: 100%;
  }
  &__body {
    min-height: 800px;
    padding: 30px;
    &:not(.started) {
      padding-bottom: 50px !important;
    }
    &.started {
      padding: 30px;
      padding-bottom: 0;
    }

    width: 100%;
    background-color: #fff;
    border-radius: 8px;
    padding-bottom: 0;
  }
  &__button {
    border: solid 1px black;
    padding: 10px;
    border-radius: 12px;
    background-color: $color-main;
    display: inline-block;
    &:hover {
      cursor: pointer;
    }
    &.action {
      color: #fff;
      text-transform: uppercase;
    }
  }
  &__choice {
    transition: all 0.2s linear;
    width: 90%;
    margin: 0 auto;
    background-color: white;
    &.correct {
      background-color: rgba(161, 242, 147, 1);
    }
    &.wrong {
      background-color: rgba(255, 103, 103, 1);
    }
  }
  &__container {
    position: relative;
  }
  &__image {
    border-radius: $border-image;
    max-width: 40%;
    padding: 20px;
    margin: 0 auto;
    box-shadow: 0 0 4px 0 grey;
    margin-bottom: 50px;
    img {
      overflow: hidden;
      border-radius: $border-image;
      width: 100%;
    }
  }
  &__choices {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    width: 100%;
  }
  &__stats {
    width: 100%;
  }
  &__progress {
    width: 100%;
    border-radius: 8px;
    height: 15px;
    overflow: hidden;
    background-color: #cbcbcb;
  }
  &__progress-slider {
    width: var(--progress);
    height: 100%;
    background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(24,121,9,1) 35%, rgba(0,212,255,1) 100%);
  }
  &__answer {
    &-placeholder {
      height: 150px;
      width: 100%;
    }
  }
  &__end {
    img {
      width: 50%;
    }
  }
  &__title {
    border: solid 1px black;
    padding: 10px;
    border-radius: 12px;
    margin-bottom: 20px;
  }
  &__title, &__answer {
    padding: 10px;
  }
  &__answer {
    width: 80%;
    margin: 0 auto;
  }
  &__answer p {
    padding-bottom: 10px;
  }

  &__start {
    padding: 50px;
    align-items: center;

    display: flex;
    flex-direction: column;
    row-gap: 20px;

    p {
      font-weight: 600;
      text-transform: uppercase;
    }
    img {
      border-radius: 8px;
      max-width: 300px !important;
      margin: 0 auto;
    }
    .quiz__button {
      flex-shrink: 0;
      flex-grow: 0;
    }
  }
}

@include mobile {
  .quiz {
    width: 100vw;
    margin-left: calc(var(--padding) * -2);

    &__body {
      box-shadow: none !important;
      padding: 30px 10px !important;
      border-radius: 0;
    }
    &__start {
      padding: 10px;
      padding-top: 50px;
    }
    &__image {
      padding: 5px;
      max-width: 70%;
      margin-bottom:20px;
    }
    &__title, &__button {
      padding: 4px;
      width: 100%;
    }
  }
}

.quiz-enter {
  opacity: 0;
  //transform: translateX(100px);
}
.quiz-enter-active {
  opacity: 1;
  //transform: translateX(0);
  transition: all 300ms;
}
.quiz-exit {
  display: none;
}
.quiz-exit-active {
  display: none;
}