
          @import "src/styles/_variables.scss";
          @import "src/styles/_mixins.scss";
        

img {
    border-radius: 8px;
    display: block;
    max-height: 100%;
}

.nav__button.view {
    max-width: 150px;
    margin: -20px auto 20px auto;

    a {
        font-size: 20px;
        padding: 8px 10px;
    }
}

.carousel {
    .title {
        font-size: 20px !important;
    }
}

.item-container {
    max-width: $container-width;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    //row-gap: var(--padding);
}

.section {
    width: 100%;
    padding: 0 var(--padding);

    &.wide {
        //padding: var(--padding) 0;
        .item-container {
            max-width: 100%;
        }
    }

    &.yl {
        background-color: rgba(217, 217, 217, 1);
    }

    &.bl {
        p {
            color: #fff;
        }

        background-color: $color-main;
    }

    &:not(&.intro) {
        @include desktop {
            padding-top: 100px;
        }
        img {
            max-width: 100%;
            display: block;
        }
    }
    &.index-3{
        .description {
            flex-direction: row-reverse;

        }
    }
    &.index-3, &.index-4, &.index-5 {
        padding-top: 0 !important;
        .description {
            .text {
                padding: 0 !important;
            }
        }
    }

    .title.link {
        &:not(&:empty) {
            margin-bottom: 50px;
        }
    }
}
$s: 40px;
.description.index-0 .index-0 {
    //box-sizing: content-box;
    position: relative;
    &:after, &:before {
        content: '';
        display: block;
        border: 5px solid black;

        position: absolute;
    }
    &:after {
        border-right: none;
        border-left: none;
        width: calc(100% + $s);
        height: 100%;
        top: 0;
        left: -$s / 2 + 5;
    }
    &:before {
        border-bottom: none;
        border-top: none;
        width: 100%;
        height: calc(100% + $s);
        top: -$s / 2 + 5;
        left: 0;
    }
    padding: 20px;
}

.intro {
    padding-bottom: 0 !important;
    background-color: rgba(0, 0, 0, 0) !important;
    position: relative;

    .intro-img {
        width: 100vw;
        margin-left: calc(var(--padding) * -1);
        margin-top: calc(var(--padding) * -1);
        border-radius: 0;
    }
    .intro-img__filter {
        background-color: rgba(0, 0, 0, 0.3);
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
    }

    .item-container {
        position: absolute;
        top: 30%;
        left: 0;
        width: 100%;
        max-width: 100vw;
        p{
            text-align: center;
        }
    }
}

p {
    text-align: justify;
    font-size: 22px;
}

.title {
    font-size: 36px;
    font-weight: 600;

    &.link {
        display: inline;
        width: auto;
        flex-grow: 0;

    }
}

.genial.section, .all.section {
    padding: 150px 50px;
    @include mobile {
        padding: 50px 20px;
    }
}

$flex-gap: 10px;

.description {
    display: flex;

    .text {
        width: 50%;

        > p {
            margin-top: 50px;
        }
    }

    &.big {
        margin-bottom: 50px;
    }

    .image-wrapper {
        width: 50%;
        flex-shrink: 0;
        img {
            width: 100%;
            align-self: flex-start;
        }

        &.flex {
            display: flex;
            column-gap: $flex-gap;
        }

        &.big {
            position: relative;

            img {
                position: absolute;
                width: auto;
                scale: 2;
            }
        }
    }

    &.reversed {
        flex-direction: row-reverse;

        .text {
            padding-left: var(--padding);
        }
    }

    &:not(&.reversed) {
        .text {
            padding-right: var(--padding);
        }
    }

    &.index-0 {
        justify-content: center;
        .text {
            width: 80%;
        }
    }

    &.index-1 {
        flex-direction: row-reverse;
        .image-wrapper {
            width: 30%;
        }
        .title, .index-0 {
            margin-top: 0 !important;
        }
        .text {
            width: 70%;
            padding-left: var(--padding);
        }
    }
    &.index-2 {
        .title {
            margin-top: 0 !important;
        }
        .text {
            width: 60%;
        }
        .image-wrapper {
            width: 40%;
        }
    }
    &.index-3 {
        margin-top: 50px;
        .index-0, .title, .subtext {
            margin-top: 0;
        }
    }
    &.index-4 {

    }
}

.text {
    position: relative;
}

.text.extra {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
}

.subtext {
    margin-top: 50px;
    font-weight: 300;
}

.shadow {
    box-shadow: 0 0 2px 0 grey;
}

.print-wrapper {
    height: 100%;
    padding: 0 20px;
    //display: flex;
    flex-direction: column;
    align-items: center;

    .title {
        margin-top: 15px;
        font-size: 24px;
    }

    p {
        text-align: left;
    }

    .gravur {
        height: calc(100vw / 3 * 1.1);

        width: 100%;
        background-color: rgba(255, 255, 255, 0.2);
        padding: 20px 50px;
        //border-radius: 8px;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
            //flex-grow: 1;
        }
        &:hover {
            cursor: pointer;
            opacity: 0.9;
            transition: all 0.2s ease-out;
        }
    }
}

.genial .wrapper {
    display: flex;
    column-gap: 50px;

    .text {
        display: flex;
        flex-direction: column;
        justify-content: center;

        .title {
            text-align: center;
            padding-bottom: var(--padding);
        }
    }

    .item__print-container {
        width: 50%;
    }
    .text {
        width: 50%;
    }
}

.item__print {
    height: 100%;
}

.item-list {
    transition: all 0.4s linear;

    &.hidden {
        visibility: hidden;
        opacity: 0;
    }

    &.visible {
        visibility: visible;
        opacity: 1;
    }
}
@include mobile {
    .section:not(.intro) {
        img {
            margin: 0 !important;
        }
    }
    .description {
        margin: 0 !important;
        * {
            position: initial !important;
        }

        .text, .image-wrapper {
            width: 100% !important;
        }

        flex-direction: column-reverse !important;

        img {
            width: 100% !important;
            max-width: 100% !important;
        }
        .image-wrapper {
            padding-bottom: 10px;
        }

        .text {
            flex-direction: column;
            padding: 0 !important;
        }
    }
    .gravur {
        padding: 6px !important;

        img {
            border-radius: 6px;
        }
    }
    .print-wrapper {
        padding: 0 5px !important;
    }
}

@include desktop {
    .intro {
        p {
            font-size: 96px;
            font-weight: 200;
        }

        .title {
            font-weight: 600;
        }
    }
}

.columns {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    row-gap: 50px;
    .column {
        display: flex;
        row-gap: 100px;
        justify-content: center;
        .print-wrapper {
            width: 50%;

        }
        .gravur {

            img {
                flex-grow: 0;
            }
            max-height: calc(100vw / 2 * 1.1);
        }
    }
}