
          @import "src/styles/_variables.scss";
          @import "src/styles/_mixins.scss";
        
.item__print {
  &-container {
    position: relative;
    .button {
      display: block;
      margin: 20px auto;
    }
  }
  flex-grow: 1;
  &-wrapper {
    max-height: 60vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    @include desktop {
      //width: 60vw;
      //margin-left: -20%;
    }
  }
  min-height: 100%;
  height: calc(60vh);
  iframe {
    overflow: hidden !important;
    width: 100%;
    height: 100%;
  }
  .tpd-tooltip {
    //position: fixed;
  }
  .genially-embed {
    width: 100% !important;
    height: 100% !important;
    min-width: auto !important;
  }
  .container-wrapper-genially {
    background: none;
  }

  nav > div {
    display: none !important;
  }
  position: relative;
}
.carousel .item__print {
  width: 70%;
}
.loader {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 2;
}
@include mobile {
  .wrapper {
    flex-direction: column;
    & >div {
      width: 100% !important;
    }
  }
}