
          @import "src/styles/_variables.scss";
          @import "src/styles/_mixins.scss";
        
.spinner {
    box-shadow: 0 0 2px 0 grey;
    width: 48px;
    height: 48px;
    border: 5px solid #FFF;
    border-bottom-color: #FF3D00;
    border-radius: 50%;
    transform-origin: center;
    margin: auto;

    &-wrapper.inline {
        padding: 10px 0;
        .spinner {
            animation: rotation-inline 1s linear infinite;
        }

        @keyframes rotation-inline {
            0% {
                transform: rotate(0deg);
            }
            100% {
                transform:  rotate(360deg);
            }
        }
    }
    &-wrapper.fixed {
        position: absolute;
        top: 50%;
        z-index: 30;
        left: 50%;
        .spinner {
            animation: rotation-fixed 1s linear infinite;
        }
        @keyframes rotation-fixed {
            0% {
                transform: translate(-50%, -50%) rotate(0deg);
            }
            100% {
                transform: translate(-50%, -50%) rotate(360deg);
            }
        }
    }
}