$color-action-button: rgb(54, 59, 75);
$color-form-main: rgb(219, 228, 235);

// sidebar

$color-link-background: rgb(235, 239, 242);
$color-sublist-background: rgb(219, 225, 228);

$color-current-text: white;
$color-current-background: blue;

$border-radius-global: 8px;

$style-border: 2px solid grey;
$color-main: rgba(51, 55, 64, 1);
$color-bg: rgba(246, 242, 239, 1);

$container-width: 1200px;

@mixin mobile {
  @media screen and (max-width: 1000px) {
	@content;
  }
}
@mixin mobile1 {
  @media screen and (max-width: 600px) {
    @content;
  }
}
@mixin desktop {
  @media screen and (min-width: 1000px) {
    @content;
  }
}