@import "styles/variables";

.navigation {
    top: 0;
    background-color: $color-main;
    &.hide {
        background-color: rgba(51, 55, 64, calc(min(200, var(--scroll)) / 200));
    }
    z-index: 2;
    width: 100vw;
    left: 0;
    position: fixed;
    &__inner {
        max-width: $container-width;
        display: flex;
        align-items: center;
        justify-content: center;
        a {
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            font-weight: 600;
            font-size: min(5vw, 26px);
            white-space: nowrap;
            text-decoration: none;
            color: #fff;
            padding: 20px;
            transition: all 0.1s linear;
            &:hover {
                background-color: rgb(40, 43, 50);
            }
        }
        @include mobile {
            a {
                padding: 15px 10px;
                &:hover {
                    background: none;
                }
            }
        }
    }
    &.hidden {
        position: initial;
        visibility: hidden;
    }
}